import React from 'react';
import { Card } from 'primereact/card';
import "./Cards.css";
import { NavLink } from "react-router-dom";
// import { Tag } from 'primereact/tag';
import 'primeflex/primeflex.css';


interface cardProps {
    img: string;
    title?: string;
    content?: any;
    price?: number;
    route?: string;
    contentRoute?: string;
}

function Cards(props: cardProps) {
    const header = <img className="img" alt="Card" src={props.img} />
    const footer = props.route &&
        <NavLink to={props.route} className="nav-title" >
            <button className="button">{props.contentRoute}</button>
        </NavLink >
    return (
        <Card className="Card p-mr-5 p-mb-2" title={props.title} header={header} footer={footer}>
            {console.log(props.img)}
            <p>
                {props.content}
                <br />
                {props.price && props.price + "₪"}
            </p>
        </Card>
    )


}

export default Cards;