import React, { Component } from 'react';
import allImages from '../../../assets/images/hatsImage/allImages.jpg';
import "./Hats-Items.css";
import Cards from '../../CreatingComponents/Cards/Cards';
import 'primeflex/primeflex.css';
import { Globals } from '../../../Services/Global'
import HatModel from '../Models/HatModel';
import Axios from "axios";
import HatsStore from '../../../Redux/HatsStore';
import { HatsActionType } from '../../../Redux/HatsState';


interface HatState {
    hats: HatModel[];
}

class Hats extends Component<{}, HatState> {
    public constructor(props: {}) {
        super(props);
        this.state = {
            hats: HatsStore.getState().hats
        }
    }

    public async componentDidMount() {
        try {
            if (HatsStore.getState().hats.length === 0) {
                const response = await Axios.get<HatModel[]>(Globals.storeUrl + "hats/");
                const hats = response.data;
                this.setState({ hats });
                HatsStore.dispatch({ type: HatsActionType.HatsDownloaded, payload: hats })
            }
        }
        catch (err) {
            alert(err.message);
        }
    }
    public render(): JSX.Element {
        return (
            <div className="Hats-Items">
                <Cards img={allImages} title={"Woolfit Hats"}
                    content={
                        <section>
                            <p>
                                כובע פלקספיט דלתא הוא כובע מהפכני שיא הטכנולוגיה, קל מאד, חלק, ובעל נוחות מקסימלית
                            <br />
                                <br />
                            הכובע בנוי מ-3 שכבות של מגני זיעה ועמיד לחדירה במים
                            הבד (פטנט) דוחה כתמים, אנטי בקטריאלי, ומונע מהזיעה להכתים את הבד מעל
                        </p>
                            <span>
                                הרכב בד: 92% פוליאסטר, 8% ספנדקס
                        </span>
                            <br />
                            <span>
                                קיים בצבעים: שחור, לבן ונייבי
                        </span>
                            <br />
                            <span>
                                XS/S, S/M, L/XL :קיים במידות
                        </span>
                        </section>
                    }
                />

                <div key="container" className="p-d-flex p-flex-wrap container">
                    {this.state.hats.map(hat =>
                        <Cards img={Globals.storeUrl + "hatsImage/" + hat.ImageName}
                            content={"XL/L/M"} route="/store/hats" contentRoute="Click"
                            price={hat.Price}
                        />
                    )}
                </div>

            </div>
        )
    }
}

export default Hats;
