import React, { Component } from "react";
import "./Logo.css";
import backGroundLogo from "../../../assets/images/logoimages/backgroundlogo.png";


class Logo extends Component {
    public render(): JSX.Element {
        return (
            <div className="Logo">
                <img src={backGroundLogo} alt="backGroundLogo"/>
            </div>
        );
    }
}

export default Logo;

