import React from "react";
import "./App.css";
import { BrowserRouter } from "react-router-dom";
import Logo from "../Logo/Logo";
import Menu from "../Menu/Menu";
import Routing from "../Routing/Routing";
import Footer from "../Footer/Footer";

function App() {
    return (
        <BrowserRouter>
            <div className="App">
                <header>
                    <Logo />
                </header>
                <main>
                    <Routing />
                </main>
                <aside>
                    <Menu />
                </aside>
                <footer>
                    <Footer />
                </footer>
            </div>
        </BrowserRouter>
    );
}

export default App;

