import React from "react";
import { NavLink } from "react-router-dom";
import "./Menu.css";
import navImage from "../../../assets/images/logoimages/navImage.png";
import Links from '../../CreatingComponents/Links/Links';

function Menu(): JSX.Element {
    return (
        <div className="Menu">
            <div>
                <Links content={"דף הבית"} route={"/home"} src={navImage} />
                <Links content={"שיטת וולפיט"} route={"/method"} src={navImage} />
                <Links content={"חנות המותג"} route={"/store"} src={navImage} />
            </div>
        </div>
    );
}

export default Menu;
